{
padding :0;
margin :0;
color:white;

}

.HeaderBody{
    background-color: #15384C;
    height: 60px;
    width: 100%;
    color: white;
    top: 0;

    z-index: 999;
    position: fixed;
}
.navBar a{
    text-decoration: none;
    color: white;
}

.navBarMain li{

    list-style-type: none;
}

.navBar{

    display: flex;
    text-align: center;
    justify-content: space-around;
    margin-top: 25px;
    transition: 0.3s;

}
.navBarMain {
    transform: translateY(-20px);

    transition: 0.1s;
    display: flex;
    text-align: center;
    justify-content: space-around;
    width: 600px;

}
.navBarMain a{
    padding: 0 10px;
    position: relative;

}

.navBarMain .moreList{



}

.navBarMain .more{
    position: absolute;
    transform: translateX(-60px);
}
.navBarMain .moreList .test li{
   margin-top: 20px;


}
.navBarMain .moreList .test{

    border-radius: 8px;
    background-color: #15384C;
    background: rgba(21,56,76,0.7);
    width: 130px;
    padding: 20px;
    padding-right: 10px;
    text-align: left;
display: none;
}
.symbol{

}
.navBarMain .moreList:hover .test{
display: block;
}
.navBarMain a::before{

    content: "";
    position: absolute;
    width: 3px;
    height: 2px;
z-index: 999;
    background-color : #20BEC6;
    transition: 0.3s;
    margin-right: 25px;
    left: 10%;
    top: 35px;
    visibility: hidden;

}

.navBar a:hover{
    color: #20BEC6;
    cursor: pointer;
    transition: 0.3s linear;

}



.navBarMain a:hover::before{
    width: 80%;
    visibility: visible;
}
.navBarOther{
    display: flex;
    text-align: center;
    justify-content: space-around;
    width: 200px;
    margin-right: 50px;
    transform: translateY(-5px);
    flex-direction: row;


}
.navBarOther a{

}

.logo{
    flex-wrap: wrap;
    z-index: 999;
    top: 0;
    transition: 0.3s;
    transform: translateY(-45px);
    min-width: 150px;
    height: 100px;
    width: 190px;
    margin-left: 15px;

cursor: pointer;

}
@media screen and (max-width: 880px) {
    .navBarMain{
        width: 10px;
        visibility: hidden;


    }

}
@media screen and (max-width: 1000px) {
    .navBarMain li:nth-child(5) {
        display: none;

    }

    .navBarMain li:nth-child(7) {
        display: none;

    }
}
  